@media (max-width: 767px) {
  nav.menu, nav.menu--down {
    position: absolute;
    z-index: 99;
    height: calc(100vh - 150px);
    width: 70vw;
    right: 0;
    background: rgba(248, 248, 248, 0.9);
    display: none;
  }
  .menu ul, .menu--down ul {
    display: flex;
    flex-direction: column;
  }
  .menu li, .menu--down li {
    font-size: 20px;
    padding-left: 10px;
  }
  .menu .active, .menu--down.active, .menu a:hover, .menu li:hover, .menu--down a:hover, .menu--down li:hover {
    background: none;
    color: var(--bg-color-1);
  }
  .menu {
    top: 120px;
    background: black;
  }
  .menu--down {
    top: 174px;
  }
  .menu--sandwich {
    display: block;
  }
  .menu--box, .menu--box--down {
    max-width: 100%;
    width: 100%;
  }
  .menu--container, .menu--container-down {
    padding: 20px;
    box-sizing: border-box;
  }
  .menu--top, .menu--top-content {
    flex-wrap: wrap;
  }
  .menu--up {
    margin-top: -55px;
    /* background: var(--color-white); */
  }
}

